@import "./src/global.scss";



.SkillsHeader{
    margin-top: 70px;
    background: url("code.jpg") center no-repeat;
    background-size: cover;
}
.between{
    min-height: 35vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;

    @include mobile{
        min-height: 15vh;
        height: 15vh;
    }

    h1{
       // top: 45px;
        //overflow-x:visible !important;
        padding-left: 5px;
        font-size: 40px;
        color:#fff;
       
        @include mobile{
           // top:30px;
            font-size: 24px;
        }
        
        &::before {
                content: "|";
                color:#a10e06;
                font-size: 50px;
                font-weight: 500;
                padding-right: 5px;
                @include mobile{
                    font-size: 30px;
                }
            }
    }

}
.skills{
    //margin-top: 70px;
    //padding-top: 70px;
    //position: relative;
    height:110vh;
   // background-color: #e2e5e8;
    //background-color: #001e36;
    background-image: linear-gradient(to top, white, #e2e5e8);
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x:visible !important;
    //justify-content: space-around;
   
    ul{
        top:40px;
        //overflow-x:visible !important;
        margin: 10px;
        padding: 0;
        list-style: none;
        display:inline-flex;
        @include mobile{
            margin: 5px;
            font-size: 12px;
            flex-wrap: wrap;
            justify-content: center;
        }
        li{
            font-size: 16px;
            @include mobile{
                padding: 2px;
                font-size: 12px;
            }
        }
    }

    .container{
        max-width:90%;
        gap:1rem;
        top:40px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        //background-color: #a10e06;
        
        overflow-x:visible !important;
        & *{
            overflow-x:visible !important;
        }
        @include mobile{
            width: 100%;
        }
        
        .item{
            width: 180px;
            height: 85px;
            //gap: 20rem;
            border-radius: 20px;
            //border: 1px solid $mainColor;
            background-color: #f2f2f2;
            margin: 10px 0 0 0;
            display: flex;
            flex-direction:  column;
            align-items: center;
            justify-content: center;
            color:#343434;
            //position: relative;
            transition: all .5s ease;
            cursor: pointer;
           box-shadow: 2px 2px 3px #9a9a9a;

            @include mobile{
                border-radius: 10px;
                width: 90px;
                height: 50px;
               // padding-left:5px;
                
            }
            // .pBar{
            //     width: 100px;
            //     height: 100px; 
            //     @include mobile{
            //         width: 60px;
            //         height: 60px;
            //     }
            // }
            h3{
               //position:relative;
                //position:absolute;
                font-size: 16px;
                font-weight: 500;
                color:#3267ca;
                @include mobile{
                    font-size: 10px;
                }
            }

            .exp{

                text-align: center;
                
                font-size: 12px;
                @include mobile{
                    font-size: 9px;
                  // display: none;
                }
            }

            img{
                //width:200px;
                height: 150px;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                color:white;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }

            }

        }
    }
}